}

.form__selectors-wrapper {
  display: flex;
  flex-direction: column;
}

.form__selector-label {
  position: relative;
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  margin-bottom: 20px;
  cursor: pointer;
}

.form__selector--radio {
  position: absolute;
  top: 50%;
  left: 10%;
  display: block;
  width: 22px;
  height: 22px;
  margin-top: -11px;
  margin-left: -11px;
  border-radius: 50%;
}

.form__selector--checkbox {
  position: absolute;
  top: 50%;
  left: 10%;
  display: block;
  width: 24px;
  height: 16px;
  margin-top: -8px;
  margin-left: -12px;
}

.form__selector-indicator--radio {
  background-color: #f2f2f2;
  color: #333333;
}

.form__selector--radio:checked + .form__selector-indicator:active::before {
  opacity: 0.3;
}

.form__selector--checkbox:checked + .form__selector-indicator::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 24px;
  height: 16px;
  margin-top: -8px;
  margin-left: -12px;
}

.form__selector--checkbox:checked + .form__selector-indicator:active::before {
  opacity: 0.3;
}

.form__selector--radio:checked ~ .fakeRadio .fakeRadio_check {
  display: block;
}

.fakeRadio {
  border-radius: 50%;
  background: #f2f2f2;
  width: 46px;
  height: 46px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fakeRadio_check {
  display: none;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #000000;
}

.form__selector-label input {
  display: none;
}

